.dropdown-menu.nav-dropdown-menu {
  padding: 0 !important;
  border: none !important;
  margin-left: calc(#{$sidebar-mini-width} - .285rem) !important;
  background: transparent !important;
  .dropdown-item {
    background-color: $sidebar-light-submenu-bg !important;
    &.nav-link {
      p {
        margin-left: 0 !important;
        color: $sidebar-light-submenu-color !important;
        > .right {
          visibility: hidden;
        }
      }
      &.active {
        background-color: $sidebar-light-submenu-active-bg !important;
        p {
          color: $sidebar-light-submenu-active-color !important;
        }
      }
      &:hover {
        p {
          color: $sidebar-light-submenu-hover-color !important;
        }
        background-color: $sidebar-light-submenu-hover-bg !important;
      }
    }
  }
  .nav-link {
    > p {
      display: inline !important;
      visibility: visible !important;
      animation-name: unset !important;
      animation-duration: unset !important;
      animation-fill-mode: unset !important;
    }
  }
}
